.collections__wrapper .collections .collections__inner{width:100%;margin:0 auto;padding:30px 20px;display:grid;grid-template-columns:repeat(2,1fr);gap:40px 30px;align-items:center;justify-content:space-around}@media (min-width:480px){.collections__wrapper .collections .collections__inner{grid-template-columns:repeat(3,1fr)}}@media (min-width:600px){.collections__wrapper .collections .collections__inner{padding:70px 50px}}@media (min-width:1080px){.collections__wrapper .collections{width:1000px;margin:0 auto}.collections__wrapper .collections .collections__inner{padding:70px 0;gap:60px 110px;grid-template-columns:repeat(4,1fr)}}@media (min-width:1280px){.collections__wrapper .collections{width:1200px}.collections__wrapper .collections .collections__inner{grid-template-columns:repeat(4,1fr)}}